<template>
  <div v-if="ordersList != null">
    <!--  -->
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryOrdersInfo.query" clearable @clear="getOrdersList">
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" style="width: 100%">
        <el-table-column label="" type="index">
        </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.order_pay === '1'" type="primary"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" @click="showAdressDialog"></el-button>
            <el-button type="success" icon="el-icon-location" @click="logisticsDialogVisible = true"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!-- <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryOrdersInfo.pagenum" :page-sizes="[10, 15, 20, 30]" :page-size="queryOrdersInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination> -->
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="adressDialogVisible" width="50%">
      <el-form :model="adressData" :rules="adressRules" ref="adressRef" label-width="100px">
        <el-form-item label="/">
          <el-cascader v-model="adressData.princeAdress" :options="citydata" :props="{ expandTrigger: 'hover' }" @change="handleChange"></el-cascader>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="adressData.detailAdress"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="adressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="adressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
  title=""
  :visible.sync="logisticsDialogVisible"
  width="50%">
   <el-timeline :reverse="true">
    <el-timeline-item
      v-for="(activity, index) in logisticsData"
      :key="index"
      :timestamp="activity.time">
      {{activity.context}}
    </el-timeline-item>
  </el-timeline>
  <span slot="footer" class="dialog-footer">
    <el-button @click="logisticsDialogVisible = false"> </el-button>
    <el-button type="primary" @click="logisticsDialogVisible = false"> </el-button>
  </span>
</el-dialog>
   
  </div>
</template>

<script>
import citydata from './citydata'
export default {
  data() {
    return {
      //
      queryOrdersInfo: {
        query: '',
        pagenum: 1,
        pagesize: 15
      },
      // 
      ordersList: [],
      //
      total: '',
      //
      citydata,
      adressDialogVisible: false,
      adressData: {
        princeAdress: [],
        detailAdress: ''
      },
      adressRules: {

      },
      logisticsDialogVisible: false,
      //
      logisticsData: [
    {
      "time": "2018-05-10 09:39:00",
      "ftime": "2018-05-10 09:39:00",
      "context": ",,",
      "location": ""
    },
    {
      "time": "2018-05-10 08:23:00",
      "ftime": "2018-05-10 08:23:00",
      "context": "[]  95338",
      "location": ""
    },
    {
      "time": "2018-05-10 07:32:00",
      "ftime": "2018-05-10 07:32:00",
      "context": " []",
      "location": ""
    },
    {
      "time": "2018-05-10 02:03:00",
      "ftime": "2018-05-10 02:03:00",
      "context": "[], []",
      "location": ""
    },
    {
      "time": "2018-05-09 23:05:00",
      "ftime": "2018-05-09 23:05:00",
      "context": " []",
      "location": ""
    },
    {
      "time": "2018-05-09 21:21:00",
      "ftime": "2018-05-09 21:21:00",
      "context": "[], []",
      "location": ""
    },
    {
      "time": "2018-05-09 13:07:00",
      "ftime": "2018-05-09 13:07:00",
      "context": " ",
      "location": ""
    },
    {
      "time": "2018-05-09 12:25:03",
      "ftime": "2018-05-09 12:25:03",
      "context": "",
      "location": ""
    },
    {
      "time": "2018-05-09 12:22:24",
      "ftime": "2018-05-09 12:22:24",
      "context": "HLA",
      "location": ""
    },
    {
      "time": "2018-05-08 21:36:04",
      "ftime": "2018-05-08 21:36:04",
      "context": "",
      "location": ""
    }
  ]
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList() {
      const { data: res } = await this.$http.get('/orders', {
        params: this.queryOrdersInfo
      })
      if (res.meta.status != 200) return this.$message.error('')
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    //
    handleSizeChange(newSize) {
      this.queryOrdersInfo.pagesize = newSize
    },
    handleCurrentChange(newPage) {
      this.queryOrdersInfo.pagenum = newPage
    },
    //
    showAdressDialog() {
      this.adressDialogVisible = true
    },
    handleChange(){

    }
  },
}
</script>

